@keyframes load {
  to {
    transform: rotate(360deg);
  }
}

.btn {
  border: none;
  border-radius: 5px;
  min-width: 50px;
  min-height: 25px;
  font-size: 12px;
  font-weight: bold;
  text-decoration: none;
}

.btn-sync {
  color: #282828;
  background: none;
  border: 1px solid #00e500;
  margin-top: 5px;
}

.btn-sync:hover {
  cursor: pointer;
  background: #f2f2f2;
}

.btn-sync:hover .sync {
  animation: 1s load;
}

.koin-notice {
  border-radius: 5px !important;
  min-height: 40px !important;
}

.koin-notice p {
  top: 50%;
}

.sync {
  margin-right: 5px;
}

.sync-activate {
  animation: 2s infinite forwards load;
}

#koin-order-container h3 {
  text-decoration: underline #00e500;
}

.koin-popup-response {
  display: none;
}

.koin-popup-response-active {
  z-index: 9998;
  background-color: #000000b3;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  padding-top: 100px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.koin-popup-response-active .koin-popup-body {
  background-color: #fff;
}

.koin-popup-response-active img {
  width: 150px;
}

.koin-popup-response-active .close {
  color: #fff;
  z-index: 9999;
  font-size: 40px;
  font-weight: bold;
  transition: all .3s;
  position: absolute;
  top: 60px;
  right: 35px;
}

.koin-popup-response-active .close:hover, .koin-popup-response-active .close:focus {
  color: #bbb;
  cursor: pointer;
  text-decoration: none;
}

.wrap-koin {
  background-color: #fff;
  border-radius: 15px;
  padding: 30px;
}

.wrap-koin h2 {
  margin-top: 30px;
  text-decoration: underline #00e500;
}

.wrap-koin img {
  width: 80px;
  margin-bottom: -5px;
}

.wrap-koin .description a {
  color: #000;
  text-decoration: underline #00e500;
}

.wrap-koin .description a:hover {
  color: #00e500;
}

.wrap-koin .description i {
  margin-left: 5px !important;
}

.wrap-koin .woocommerce-help-tip {
  color: #00e500;
}

.wrap-koin .nav-tab {
  background: none;
  border-radius: 5px 5px 0 0;
}

.top-disable {
  display: none;
}

.warning i {
  color: #e6b800;
}

.webhook {
  background-color: #fff;
  border: 1.9px solid #4dff4d;
  border-radius: 5px;
  width: 70%;
  height: 60px;
  padding: 15px 20px;
}

.webhook .regular-input {
  background-color: #e6ffe6;
  border: none;
  outline: none;
  font-size: 13px;
  font-style: italic;
  width: 90% !important;
  padding-left: 15px !important;
}

.webhook .fas {
  float: right;
  color: #4d4d4d;
  background-color: #0000;
  border: 1px solid #00e500;
  border-radius: 5px;
  padding: 7px;
  font-size: 14px;
  position: relative;
  bottom: 67px;
  right: 30px;
}

.webhook .fas:hover {
  color: #fff;
  cursor: pointer;
  background-color: #4d4d4d;
  transition: all .3s;
}

.webhook .description {
  color: #000;
  margin-top: 10px;
  text-decoration: underline #00e500;
}

.koin-installments-container-context {
  grid-template-columns: 160px 160px;
  display: grid;
}

.koin-installments-container-context .koin-installments-container-fields {
  padding: 3px 0;
  display: flex;
}

.koin-installments-container-context .koin-installments-container-fields span {
  width: 20px;
}

.koin-installments-container-context .koin-installments-container-fields input {
  text-align: right;
  width: 100px !important;
  height: 25px !important;
}

.koin-installments-container-context .koin-installments-container-fields input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.koin-installments-container-context .koin-installments-container-fields input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.koin-installments-container-context .koin-installments-container-fields input[type="number"] {
  -moz-appearance: textfield;
}

.deactive {
  display: none !important;
}
